import { Section } from 'react-scroll-section';
import { marketingContacs } from '../../../../data';
import ContactCard from './ContactCard';

export default function EmployerMarketingContacts() {
  return (
    <Section
      id="contacts"
      className="mb-3 flex min-h-[500px] items-center px-6 font-primary lg:px-24"
    >
      <div className="flex-1">
        <h3 className="mb-5 text-center text-xl font-semibold text-primary">
          Contact Our Marketing Teams for More Information
        </h3>
        <div className="grid grid-cols-1 gap-3 md:grid-cols-2 lg:grid-cols-3">
          {marketingContacs.map((contact) => (
            <ContactCard
              key={contact.email}
              email={contact.email}
              phone={contact.phone}
              wa={contact.wa}
              name={contact.name}
            />
          ))}
        </div>
      </div>
    </Section>
  );
}
