import { Section } from 'react-scroll-section';
import { EmployerCardGroupData } from '../../../../data';
import EmployerCard from '../EmployerCard/EmployerCard';
import Heading2 from '../../../atoms/Heading2/Heading2';

export default function EmployerCardGroup() {
  return (
    <Section id="timeline" className="flex min-h-[500px] items-center">
      <div className="flex-1">
        <Heading2 title="Timeline" />
        <div className="grid grid-cols-2 gap-4 px-6 lg:grid-cols-4 lg:px-24">
          {EmployerCardGroupData.map((item) => (
            <EmployerCard
              key={item.title}
              title={item.title}
              icon={item.icon}
              content={item.content}
              color={item.color}
            />
          ))}
        </div>
      </div>
    </Section>
  );
}
