import { MarketingContact } from '../../../../interfaces';

export default function ContactCard(props: MarketingContact) {
  const { email, name, phone, wa } = props;
  return (
    <div className="text-center">
      <p className="text-md mb-1 font-semibold">{name}</p>
      <p className="mb-1 text-sm">
        <a
          target="_blank"
          rel="noreferrer"
          className="text-gray-800 underline underline-offset-2 hover:text-gray-800/80"
          href={wa}
        >
          {phone}
        </a>
      </p>
      <p className="mb-1 text-sm">
        <a
          target="_blank"
          rel="noreferrer"
          className="text-gray-800 underline underline-offset-2 hover:text-gray-800/80"
          href={`mailto:${email}`}
        >
          {email}
        </a>
      </p>
    </div>
  );
}
