import { useState } from 'react';
import { Section } from 'react-scroll-section';
import useSWR from 'swr';
import { IPricingResponse } from '../../../interfaces';
import { fetcher } from '../../../utils';
import Heading2 from '../../atoms/Heading2/Heading2';
import EmployerPricingCard from '../../molecules/Employer/EmployerPricingCard/EmployerPricingCard';
import PricingToggle from './PricingToggle';
import pricing from '../../../data/pricing.json';

export default function EmployerPricing() {
  const [isCareer, setIsCareer] = useState<boolean>(true);
  const [isToggling, setIsToggling] = useState(false);
  const { data, error } = useSWR<IPricingResponse>(
    [`${process.env.REACT_APP_API_URL}/pricing`, {}],
    fetcher
  );

  return (
    <Section
      id="pricing"
      className="mx-auto mt-8 min-h-screen items-center px-6 lg:px-24"
    >
      <div className="flex-1">
        <Heading2 title="Pricing" />
        <PricingToggle
          setIsToggling={setIsToggling}
          isCareer={isCareer}
          setIsCareer={setIsCareer}
        />
        <div className="flex flex-wrap justify-center">
          {!isToggling &&
            pricing.career.map((item) => (
              <EmployerPricingCard
                key={item.id}
                features={item.features}
                id={item.id}
                type={item.type}
                price={item.price}
              />
            ))}
          {!isToggling &&
            !isCareer &&
            pricing.scholarship.map((item) => (
              <EmployerPricingCard
                key={item.id}
                features={item.features}
                id={item.id}
                type={item.type}
                price={item.price}
              />
            ))}
          {isToggling && (
            <>
              <div className="h-[300px] w-full overflow-hidden p-3 font-primary duration-150 hover:-translate-y-1 md:w-1/2 lg:w-1/3">
                <div className="h-[300px] w-full animate-pulse overflow-hidden rounded-xl bg-gray-100" />
              </div>
              <div className="h-[300px] w-full overflow-hidden p-3 font-primary duration-150 hover:-translate-y-1 md:w-1/2 lg:w-1/3">
                <div className="h-[300px] w-full animate-pulse overflow-hidden rounded-xl bg-gray-100" />
              </div>
              <div className="h-[300px] w-full overflow-hidden p-3 font-primary duration-150 hover:-translate-y-1 md:w-1/2 lg:w-1/3">
                <div className="h-[300px] w-full animate-pulse overflow-hidden rounded-xl bg-gray-100" />
              </div>
              <div className="h-[300px] w-full overflow-hidden p-3 font-primary duration-150 hover:-translate-y-1 md:w-1/2 lg:w-1/3">
                <div className="h-[300px] w-full animate-pulse overflow-hidden rounded-xl bg-gray-100" />
              </div>
              <div className="h-[300px] w-full overflow-hidden p-3 font-primary duration-150 hover:-translate-y-1 md:w-1/2 lg:w-1/3">
                <div className="h-[300px] w-full animate-pulse overflow-hidden rounded-xl bg-gray-100" />
              </div>
            </>
          )}
        </div>
      </div>
    </Section>
  );
}
