import { Helmet } from 'react-helmet';
import { ScrollingProvider } from 'react-scroll-section';
import EmployerAboutIVF from '../../molecules/Employer/EmployerAboutIVF/EmployerAboutIVF';
import EmployerCardGroup from '../../molecules/Employer/EmployerCardGroup/EmployerCardGroup';
import EmployerCarousel from '../../molecules/Employer/EmployerCarousel/EmployerCarousel';
import EmployerHeader from '../../molecules/Employer/EmployerHeader/EmployerHeader';
import EmployerJoin from '../../molecules/Employer/EmployerJoin/EmployerJoin';
import EmployerMarketingContacts from '../../molecules/Employer/EmployerMarketingContacts/EmployerMarketingContacts';
import EmployerPricing from '../../organisms/EmployerPricing/EmployerPricing';
import Footer from '../../molecules/Footer/Footer';
import EmployerHomeDecorations from '../../organisms/EmployerHomeDecorations/EmployerHomeDecorations';
import { titlePageGenerator } from '../../../utils';
import AnimatedPage from '../../containers/AnimatedPage/AnimatedPage';

function EmployerPage() {
  return (
    <ScrollingProvider>
      <Helmet>
        <title>{titlePageGenerator('Employer Page')}</title>
        <meta name="description" content="description here" />
      </Helmet>
      <EmployerHeader />
      <main className="relative overflow-x-hidden">
        <EmployerCarousel />
        <AnimatedPage>
          <EmployerAboutIVF />
          <EmployerCardGroup />
          <EmployerPricing />
          <EmployerJoin />
          <div className="h-2" />
          <EmployerMarketingContacts />
          <EmployerHomeDecorations />
        </AnimatedPage>
      </main>
      <Footer />
    </ScrollingProvider>
  );
}

export default EmployerPage;
