import { EmployerCardProps } from '../../../../interfaces';

export default function EmployerCard(props: EmployerCardProps) {
  const { content, icon, title, color } = props;

  const cardClasses = `relative rounded-lg shadow ${
    color === 'blue' ? 'bg-gradient-to-b to-primary from-ivfDark' : 'bg-white'
  } p-5 font-primary ${color === 'blue' ? 'text-white' : 'text-primary'}`;

  return (
    <div className={`duration-150 hover:-translate-y-1 ${cardClasses}`}>
      <i
        className={`bi bi-${icon} absolute right-3 top-3 z-10 text-8xl ${
          color === 'blue' ? 'text-white/10' : 'text-warning/30'
        }`}
      />
      <div className="z-20 mb-2 flex items-center gap-2">
        <i className="bi bi-calendar-week-fill" />
        <span>{title}</span>
      </div>
      <div
        className="z-20 font-medium"
        dangerouslySetInnerHTML={{ __html: content }}
      />
    </div>
  );
}
