import { Section } from 'react-scroll-section';

export default function EmployerJoin() {
  return (
    <Section
      id="join"
      className="my-10 mx-auto w-[95%] rounded-lg bg-primary py-6 px-6 text-center lg:w-3/5 "
    >
      <h3 className="mb-3 bg-gradient-to-r from-white to-warning bg-clip-text font-primary text-3xl font-black text-transparent ">
        Join Now To Get The Best of The Best Candidate
      </h3>
      <a
        href="https://docs.google.com/forms/d/e/1FAIpQLScqRxoTjvr8hj58BYX6Utn-xqXcgeR1SW8-Kc6NJpUXMk467w/viewform"
        className="inline-block rounded-lg bg-warning py-2 px-5 font-bold text-primary ring-warning ring-offset-2 ring-offset-primary hover:ring-2 focus:outline-none focus:ring-2 active:ring-2"
      >
        JOIN
      </a>
    </Section>
  );
}
