import { Carousel } from 'react-responsive-carousel';
import { Section } from 'react-scroll-section';

export default function EmployerCarousel() {
  return (
    <Section
      id="home"
      className="flex items-center bg-ivfPurple2 shadow-lg"
      style={{ minHeight: 'calc(100vh - 80px)' }}
    >
      <Carousel
        showArrows
        showThumbs={false}
        autoPlay
        interval={5000}
        infiniteLoop
        stopOnHover
        swipeable
        emulateTouch
        thumbWidth={100}
        showStatus={false}
        className="mb-5 p-5 shadow lg:min-h-[500px]"
      >
        <div>
          <img
            src="/img/banners/banner-4.png"
            alt="banner-4"
            className="rounded-lg"
          />
        </div>
        <div>
          <img
            src="/img/banners/banner-4.png"
            alt="banner-4"
            className="rounded-lg"
          />
        </div>
      </Carousel>
    </Section>
  );
}
