import { EmployerPricingDataFeaturesProps } from '../../../../interfaces';
import Check from './Check';

export default function Li(props: EmployerPricingDataFeaturesProps) {
  const { description } = props;
  return (
    <li className="flex space-x-3">
      <Check />
      <span className="text-base font-normal leading-tight text-gray-500">
        {description}
      </span>
    </li>
  );
}
