import { useScrollSections } from 'react-scroll-section';

export default function MenuDekstop() {
  const sections = useScrollSections();
  return (
    <ul className="hidden flex-col justify-center gap-10 font-primary text-white lg:flex lg:flex-row">
      {sections.map(({ id, onClick, selected }) => (
        <li key={id}>
          <button
            type="button"
            onClick={onClick}
            className={`${
              selected ? 'underline underline-offset-2' : ''
            } hover:text-white/70`}
          >
            {id.toUpperCase()}
          </button>
        </li>
      ))}
    </ul>
  );
}
