interface Props {
  isCareer: boolean;
  setIsCareer: (isCareer: boolean) => void;
  setIsToggling: (isCareer: boolean) => void;
}

export default function PricingToggle(props: Props) {
  const { isCareer, setIsCareer, setIsToggling } = props;
  const toggleHandler = () => {
    setIsCareer(!isCareer);
    setIsToggling(true);
    setTimeout(() => {
      setIsToggling(false);
    }, 500);
  };

  return (
    <div className="mb-2 flex justify-center gap-3 font-primary">
      <p
        className={`flex-1 text-right ${
          isCareer && 'font-semibold text-primary'
        }`}
      >
        Career and Internship EXPO
      </p>
      <button
        className={`flex h-8 w-16 ${
          isCareer ? 'justify-start' : 'justify-end'
        } rounded-full bg-primary shadow-inner`}
        type="button"
        onClick={toggleHandler}
      >
        <div className="h-8 w-8 rounded-full bg-warning shadow-lg" />
      </button>
      <p className={`flex-1 ${!isCareer && 'font-semibold text-primary'}`}>
        Scholarship EXPO
      </p>
    </div>
  );
}
