import { useState } from 'react';
import HomeLogo from './HomeLogo';
import LogoGroup from './LogoGroup';
import MenuList from './MenuList';

export default function EmployerHeader() {
  const [isNavShow, setIsNavShow] = useState(false);
  return (
    <header className="sticky top-0 z-50 flex h-20 w-full items-center justify-between bg-ivfPurple2/95 px-6 shadow-lg backdrop-blur-lg">
      <LogoGroup />
      <MenuList showMenu={isNavShow} />
      <HomeLogo />
      <button
        type="button"
        className="flex h-10 w-10 items-center justify-center rounded-lg bg-warning text-white ring-warning ring-offset-2 ring-offset-primary focus:outline-none focus:ring-2 active:ring-2 lg:hidden"
        onClick={() => setIsNavShow(!isNavShow)}
      >
        <i className={`bi bi-${isNavShow ? 'x-lg' : 'list'}`} />
      </button>
    </header>
  );
}
