import { useScrollSections } from 'react-scroll-section';

export default function MenuMobile() {
  const sections = useScrollSections();

  return (
    <ul className="fixed right-0 top-20 left-0 block gap-10 bg-primary px-6 font-primary text-white shadow-lg transition-all duration-300">
      {sections.map(({ id, selected, onClick }) => (
        <li key={id}>
          <button
            type="button"
            onClick={onClick}
            className={`${
              selected ? 'underline underline-offset-2' : ''
            } hover:text-white/70`}
          >
            {id.toUpperCase()}
          </button>
        </li>
      ))}
    </ul>
  );
}
