import { Section } from 'react-scroll-section';
import Heading2 from '../../../atoms/Heading2/Heading2';

export default function EmployerAboutIVF() {
  return (
    <Section
      id="about"
      className="mx-auto mb-10 flex min-h-[500px] items-center px-6 font-primary lg:mt-12 lg:w-3/5"
    >
      <div className="flex-1">
        <Heading2 title="About IPB Virtual Fair" />
        <p className="text-center">
          Sub Direktorat Pengembangan Karir atau biasa dikenal Career
          Development &amp; Assessment IPB University (CDA – IPB), merupakan sub
          unit dibawah Direktorat Kemahasiswaan dan Pengembangan Karir di IPB
          University mempunyai misi untuk mengembangkan dan menghantarkan
          lulusan-lulusan IPB pada khususnya dan lulusan perguruan tinggi pada
          umumnya untuk berkarir sesuai dengan passion dan skill masing- masing.
        </p>
      </div>
    </Section>
  );
}
