import { IPricingCard } from '../../../../interfaces';
import RupiahFormat from '../../../atoms/RupiahFormat/RupiahFormat';
import ButtonChoosePlan from './ButtonChoosePlan';
import Li from './Li';

export default function EmployerPricingCard(props: IPricingCard) {
  const { features, id, type, price } = props;
  return (
    <div
      id={id.toString()}
      className="w-full overflow-hidden p-3 font-primary duration-150 hover:-translate-y-1 md:w-1/2 lg:w-1/3"
    >
      <div className="flex h-full flex-col justify-between rounded-lg border border-primary bg-white p-4 shadow-sm duration-150 hover:shadow-md sm:p-8">
        <div>
          <h5 className="mb-4 text-4xl font-semibold text-primary">{type}</h5>
          <ul className="my-7 space-y-5">
            {features.map((feature) => (
              <Li key={feature} description={feature} />
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}
